@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro);
@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);
@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  background-color: #f3f2f1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: calc(10px + 2vmin);
  color: black;
  font-family: sans-serif;
  /* background-image: url("images/background.gif");
  background-size: contain; */
}

.App-welcome {
  display:flex;
  justify-content: center;
}

#aboutme {
  font-size: large;
  margin: 0px 25% 0px 25%;
}

#name {
  /* font-family: 'Source Code Pro', monospace; */
  font-family: 'Press Start 2P', cursive;
  border-right: solid 3px rgba(0, 0, 0, 0.75);
  white-space: nowrap;
  overflow: hidden;
}

#profile {
  border-radius: 50%;
  margin-top: 40px;
  margin-bottom:20px;
  height: 150px;
  width: auto;
}

/* Animations */

.App-welcome, #aboutme{
  opacity: 0;
  -webkit-animation: fadeInAnimation 1.5s ease 0s 1 forwards;
          animation: fadeInAnimation 1.5s ease 0s 1 forwards;
}

#name {
  -webkit-animation: animated-text 1.25s linear 1s 1 normal both,
             animated-cursor 1s steps(29,end) infinite;
          animation: animated-text 1.25s linear 1s 1 normal both,
             animated-cursor 1s steps(29,end) infinite;
}

@-webkit-keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* text animation */
@-webkit-keyframes animated-text{
  from{width: 0;}
  to{width: 11.5em;}
}
@keyframes animated-text{
  from{width: 0;}
  to{width: 11.5em;}
}

/* cursor animations */
@-webkit-keyframes animated-cursor{
  from{border-right-color: rgba(0, 0, 0, 0.75);}
  to{border-right-color: transparent;}
}
@keyframes animated-cursor{
  from{border-right-color: rgba(0, 0, 0, 0.75);}
  to{border-right-color: transparent;}
}
#brand {
    font-family: 'Press Start 2P', cursive;
}

.navlink {
    display: inline-block;
}

.tooltiptext {
    font-size: x-large;
}

.navlink .tooltiptext {
    visibility: hidden;
    width: 6em;
    background-color: black;
    color: #fff;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    top: 90%;
    right: 11%;
    margin-left: 0px;
}

.navlink .tooltiptext::after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent black transparent;
}
  
.navlink:hover .tooltiptext {
    visibility: visible;
}
